import { Container } from "@chakra-ui/react";
import { Tab , Box , Text ,Tabs , TabList , TabPanels, TabPanel } from "@chakra-ui/react";
import { useEffect } from "react";
import { useNavigate } from "react-router";
import Login from "../components/Authentication/Login";
import Signup from "../components/Authentication/SignUp";

function Homepage() {
  const navigate = useNavigate();

  useEffect(() => {
    const user = JSON.parse(localStorage.getItem("userInfo"));

    if (user) navigate("/chats");
  }, [navigate]);

    return (
        <Container maxW="xl" centerContent>
            <Box d="flex" justifyContent = "center" p={3} bg="white" w="100%" m="40px 0 15px 0" borderRadius="lg" borderWidth="1px">
                <Text fontSize="5xl" fontFamily="Ubuntu" textAlign="center">
                    Let's Chat
                </Text>
            </Box>
            <Box bg="white" w="100%" p={4} borderRadius="lg" borderWidth="1px">
                <Tabs isFitted variant="soft-rounded">
                    <TabList mb="1em">
                        <Tab>Login</Tab>
                        <Tab>Sign Up</Tab>
                    </TabList>
                    <TabPanels>
                        <TabPanel> <Login /> </TabPanel>
                        <TabPanel> <Signup /> </TabPanel>
                    </TabPanels>
                </Tabs>
            </Box>
        </Container>
    )
}
export default Homepage;
